section {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.toTheRightSideBox {
  display: flex;
  width: 100%;
  justify-content: flex-end;

  h2 {
    text-shadow: 4px 3px 20px rgba(0, 0, 0, 0.38);
    font-size: clamp(1rem, 1vw + 0.75rem, 1.75rem);
    font-weight: 700;
    letter-spacing: -0.07rem;
    padding-left: 1rem;
    width: 100%;
    max-width: 1440px;
  }
}

.mainProjectBox {
  flex-grow: 1; /* Das Element wächst, um den verfügbaren Platz zu füllen */
  flex-shrink: 1; /* Es schrumpft, wenn der Platz nicht reicht */

  background-color: rgb(111, 114, 104);
  width: 100%;
  max-width: 1440px;
  min-height: 300px; /* Mindesthöhe für kleine Bildschirme */
  max-height: 460px; /* Maximale Höhe für große Bildschirme */
  // height: 40vh; /* Dynamische Höhe basierend auf der Bildschirmgröße */
}

